<template>
  <div class="center">
    <div class="main1">
      <div class="zong">
        <dl class="tgvn">
          <dd class="ery">另存为装修模板</dd>
        </dl>
        <div class="tgr">
          <div class="rtvmm">
            <div class="ygf">

              <div class="ertw">
                <dl class="trfv">
                  <dd class="wrty">模板描述<span class="font2">*</span></dd>
                  <dd class="yuvvv">
                    <input type="text" class="bgt" value="请填写" v-model="description">
                  </dd>
                </dl>
              </div>

              <div class="ertw">
                <dl class="trfv">
                  <dd class="wrty">适用城市<span class="font2">*</span></dd>
                  <dd class="yuvvv">
                    <select name="" class="bgt1" v-model="currCity">
                      <option v-for="city in cityList" :value="city">
                        {{city.text}}
                      </option>
                    </select>
                  </dd>
                </dl>
              </div>

            </div>

            <div class="ertc">
              <dl class="erfbb">
                <dd class="rtfv">版本号<span class="font2">*</span></dd>
                <dd class="rtffd">
                  <input name="" type="text" class="yt6b" v-model="version">
                </dd>
              </dl>
            </div>

            <div class="wrtvn">
              <dl class="ertvb">已选择：</dl>
              <div :style="{display:isCityDisplay}">
                <dl class="ung">{{currCity.text}}</dl>
                <dl class="ertvbm">
                  <a href="javascript:void(0)" @click="delCity"><img src="../../../assets/images/cha.jpg" width="22" height="22"></a>
                </dl>
              </div>
            </div>

          </div>

          <div class="rtvmm2" style=" border-bottom:none;">
            <div class="ygf22">

              <div class="ertw" style="display: none">
                <dl class="trfv">

                  <dd class="wrty">选择施工任务<span class="font2">*</span></dd>
                  <dd class="yuvvv">
                    <select name="selectedVersion" class="bgt1" v-model="selectedVersion" @change="changeVersion()" style="width:100px;">
                      <option value="">选择版本号</option>
                      <option v-for="key in selectedKeys" :value="key">{{key}}</option>
                    </select>
                  </dd>
                  <dd class="yuvvv">
                    <select name="selectedTaskItem" class="bgt1" style="width:300px;" v-model="selectedTaskItem" @change="changeVersionTask()">
                      <option :value="{}">选择施工任务</option>
                      <option v-for="item in selectedVersionTasks" :value="item">{{item.name}}</option>
                    </select>
                  </dd>
                </dl>
              </div>

              <div class="wrtvn" style="display: none">
                <dl class="ertvb">已选择：</dl>
                <div v-for="(task,index) in selectedTaskList">
                  <dl class="yubg">
                    <dd class="ung">{{task.version}} | {{task.name}}</dd>
                    <dd class="ertvbm">
                      <a href="javascript:void(0)" @click="delTask(index)"><img src="../../../assets/images/cha.jpg" width="22" height="22"></a>
                    </dd>
                  </dl>
                </div>
              </div>

              <dl class="ungjk">
                <input @click="save" type="button" class="lomn" value="保存">
              </dl>
              <dl class="ungjk" style="display: none">
                <input type="button" class="lomn1" value="立即编辑模板" @click="addTask">
              </dl>

            </div>
            <div class="wrtvn1"></div>

          </div>
        </div>
      </div>
      <div class="vcon" style="display: none;">
        <ul class="vconlist clearfix">
          <li class="select">
            <dl class="u8ik">
              <dd>1、调取步骤一标题</dd>
              <dd>步骤一描述</dd>
              <dd>2、调取步骤二标题</dd>
              <dd>步骤一描述</dd>
            </dl>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
  import axios from 'axios'
  export default {
    data() {
      return {
        description: '',
        cityList: [{
          text: "北京",
          value: "01"
        },
          {
            text: "上海",
            value: "02"
          },
          {
            text: "广州",
            value: "03"
          },
          {
            text: "深圳",
            value: "04"
          },
        ],
        currCity: '',
        version: '',
        task: '',
        isCityDisplay: 'none',
        selectedVersion: "", //选择的模板版本号
        tasksMap: {}, //初始化的任务列表，按版本号归类
        selectedKeys: [],
        selectedVersionTasks: [], //
        selectedTaskItem: {},
        taskList: [],
        selectedTask: '',
        selectedTaskList: [],
        delNum: '',
        taskIdList: '',
        flag: '',
        fitOrderId:''
      }
    },
    created() {
      this.tasksMap = {};
      this.getTaskList();
      this.fitOrderId=this.$route.params.orderId
    },
    methods: {
      addTask() {
        this.addRule();
        this.$router.push({ path: '/add_fit_task', query: {version: this.version}});
      },
      addRule() {
        var url = basePath + "/fitRule/add";
        axios.post(url, {
          city: this.currCity.value,
          task: this.taskIdList,
          version: this.version,
          description: this.description
        });
      },
      delCity() {
        this.isCityDisplay = 'none'
        this.taskIdList = ''
        this.selectedTaskList.forEach(element => {
          this.taskIdList += element.value + ','
        })

      },
      delTask(num) {
        this.selectedTaskList.splice(num, 1)
        this.taskIdList = ''
        this.selectedTaskList.forEach(element => {
          this.taskIdList += element.value + ','
        })
      },
      save() {
        var url = basePath + "/fitOrder/saveAsFitRule";
        axios({
          method:'put',
          url:url,
          data:{
            city: this.currCity.value,
            task: this.taskIdList,
            version: this.version,
            description: this.description,
            id:this.fitOrderId
          }
        }).then(response=>(
          this.$router.push({
            path: "/siderBar/fitRuleIndex"
          })
        )) },
      judge(val) {
        if (val == 1)
          alert('操作成功')
        else
          alert('操作失败')
      },
      getTaskList() {
        // let taskList = [{
        //     id: 1,
        //     name: "施工任务1",
        //     version: "V1.0"
        //   },
        //   {
        //     id: 2,
        //     name: "施工任务2",
        //     version: "V1.0"
        //   },
        //   {
        //     id: 3,
        //     name: "施工任务3",
        //     version: "V1.1"
        //   },
        //   {
        //     id: 4,
        //     name: "施工任务4",
        //     version: "V1.1"
        //   },
        //   {
        //     id: 5,
        //     name: "施工任务5",
        //     version: "V1.0"
        //   },
        //   {
        //     id: 6,
        //     name: "施工任务6",
        //     version: "V2.1"
        //   },
        //   {
        //     id: 7,
        //     name: "施工任务7",
        //     version: "V1.0"
        //   },
        //   {
        //     id: 8,
        //     name: "施工任务8",
        //     version: "V1.0"
        //   }
        // ];

        let taskList = [];
        let url = basePath + "/fit_task/getAllFitTasks"
        axios.get(url).then(
          response => {
            if (response.data) {
              for (let task of response.data) {
                taskList.push({
                  id: task.id,
                  name: task.title,
                  version: task.version
                });
              }

              if (taskList && taskList.length > 0) {
                for (let task of taskList) {
                  if (!this.tasksMap[task.version]) {
                    this.selectedKeys.push(task.version);
                    this.tasksMap[task.version] = [];
                  }
                  this.tasksMap[task.version].push(task);
                }
              }
              console.log(this.tasksMap);

            }
          }, error => {
            console.log(error);
          }
        );



      },
      changeVersion() {
        if (this.selectedVersion == "") {
          this.selectedVersionTasks = [];
        } else {
          this.selectedVersionTasks = this.tasksMap[this.selectedVersion];
        }
        this.selectedTaskItem = {};

      },
      changeVersionTask() {
        //把选择的任务加入到选择任务列表中
        if (this.selectedTaskItem.id) {
          if (this.selectedTaskList.length == 0) {
            this.selectedTaskList.push(this.selectedTaskItem);
            return;
          }
          let existed = false;
          for (let task of this.selectedTaskList) {
            if (task.id == this.selectedTaskItem.id) {
              // console.log("existed");
              existed = true;
              break;
            }
          }
          if (!existed) {
            this.selectedTaskList.push(this.selectedTaskItem);
          }
        }

      }
    },
    watch: {
      currCity: function (city) {
        this.isCityDisplay = 'block'
      },
      selectedTask: function (task) {
        this.selectedTaskList.push(task)
        this.taskIdList = ''
        this.selectedTaskList.forEach(element => {
          this.taskIdList += element.value + ','
        })
      },
      selectedVersion: {
        handler: (val, oldVal) => {
          console.log(val);

        },
        deep: true
      }
    }
  }
</script>



