<script>
  import axios from 'axios'
  import Vue from 'vue'
  Vue.filter('fix0',function(value){
     return  value.toFixed(0)
  })
  export default{
    data(){
      return {
        requirementListNo: '',
        phaseNumber: '',
        data: [],
        orderId: 1,
        days:[]
      }
    },
    watch: {
      // 如果路由有变化，会再次执行该方法
      '$route': 'fetchData'
    },
    mounted: function () {
      this.fetchData()

    },
    methods: {
      fetchData(){
        this.phaseNumber = this.$route.params.phaseNumber
        this.getPhase()
      },
      getPhase(){
        var url = basePath + "/fitOrder/notEditWorkAmount/order/" + this.orderId + "/phase/" + this.phaseNumber;
        axios.get(url
        ).then(response => (
          this.data = response.data,
            this.data.forEach((seq) => {
              seq.processFlow = JSON.parse(seq.processFlow)
              if (typeof seq.show == 'undefined')
                this.$set(seq, 'show', 'none')
            })
        ))
      },
      showRequirement(fitSequence){
        if (fitSequence.show == 'none')
          fitSequence.show = 'block'
        else
          fitSequence.show = 'none'
      }

    }


  }
</script>



