<template>
  <div class="center">
    <div class="main">
      <div class="left">
        <div class="list">
          <ul class="yiji">
            <li><a href="javascript:void(0)" class="inactive" :class="{inactives:isUserInfoOpen}"
                   v-on:click="foldUserInfo">用户信息</a>
              <ul :style="{display:ifUserInfoBlock}">
                <li>
                  <div>
                    <router-link to="/siderBar/fitRuleIndex">注册用户
                    </router-link>
                  </div>
                </li>
              </ul>

            </li>
            <li><a href="javascript:void(0)" class="inactive" :class="{inactives:isOrderManageOpen}"
                   v-on:click="foldOrderManage">订单管理</a>
              <ul :style="{display:ifOrderManageBlock}">
                <li>
                  <div v-on:click="foldUnPaid">
                    <a href="javascript:void(0)" class="inactive" :class="{inactives:isUnPaidOpen}">测量订单
                    </a>
                  </div>
                  <ul :style="{display:ifUnPaidBlock}">
                    <li>
                      <router-link to="/siderBar/fitRuleIndex">
                        待支付
                      </router-link>
                    </li>
                  </ul>
                </li>
                <li>
                  <div @click="foldFitOrder">
                    <a href="javascript:void(0)" class="inactive" :class="{inactives:isFitOrderOpen}">装修订单
                    </a>
                  </div>
                  <ul :style="{display:ifFitOrderBlock}">
                    <li>
                      <div @click="foldUnPublish">
                      <router-link to="/ownerSiderBar/fitOrderUnPublished/selectFitPlan" class="inactive" :class="{inactives:isUnPublishedOpen}">
                        订单详情-未发布计划
                      </router-link>
                      </div>
                        <ul :style="{display:ifUnEditBlock}">
                          <li>
                            <router-link to="/ownerSiderBar/fitOrderUnPublished/fitPlanUnedit">
                              未编辑工程量、材料
                            </router-link>
                          </li>
                        </ul>

                    </li>
                    <li>
                      <router-link to="/ownerSiderBar/fitOrderUnPublished/fitPlanRunning">
                        订单详情-施工中
                      </router-link>
                    </li>
                    <li>
                      <router-link to="/ownerSiderBar/fitOrderUnPublished/fitPlanRunning">
                        订单详情-暂停施工
                      </router-link>
                    </li>
                    <li>
                      <router-link to="/ownerSiderBar/fitOrderUnPublished/fitOrderCancel">
                        订单详情-撤销合同
                      </router-link>
                    </li>

                  </ul>
                </li>
              </ul>
            </li>
          </ul>
        </div>
      </div>
      <div id="right">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    data(){
      return {
        ifUserInfoBlock: 'none',
        isUserInfoOpen: false,
        ifOrderManageBlock: 'none',
        isOrderManageOpen: false,
        ifUnPaidBlock: 'none',
        isUnPaidOpen: false,
        ifFitOrderBlock: 'none',
        isFitOrderOpen: false,
        isUnPublishedOpen:false,
        ifUnEditBlock:'none'
      }

    },
    methods: {
      foldUserInfo(){
        if (this.ifUserInfoBlock == 'none') {
          this.ifUserInfoBlock = 'block'
          this.isUserInfoOpen = true
        }
        else {
          this.ifUserInfoBlock = 'none'
          this.isUserInfoOpen = false
        }
      },
      foldOrderManage(){
        if (this.ifOrderManageBlock == 'none') {
          this.ifOrderManageBlock = 'block'
          this.isOrderManageOpen = true
        }
        else {
          this.ifOrderManageBlock = 'none'
          this.isOrderManageOpen = false
        }
      },
      foldUnPaid(){
        if (this.ifUnPaidBlock == 'none') {
          this.ifUnPaidBlock = 'block'
          this.isUnPaidOpen = true
        }
        else {
          this.ifUnPaidBlock = 'none'
          this.isUnPaidOpen = false
        }
      },
      foldFitOrder(){
        if (this.ifFitOrderBlock == 'none') {
          this.ifFitOrderBlock = 'block'
          this.isFitOrderOpen = true
        }
        else {
          this.ifFitOrderBlock = 'none'
          this.isFitOrderOpen = false
        }
      },
      foldUnPublish(){
        if (this.ifUnEditBlock == 'none') {
          this.ifUnEditBlock = 'block'
          this.isUnPublishedOpen = true
        }
        else {
          this.ifUnEditBlock = 'none'
          this.isUnPublishedOpen = false
        }
      }
    }

  }
</script>



