<template>
  <div class="yt6gg">
    <div class="onmk" style="border-bottom:none;">
      <dl class="wer">
        <dd class="efgb">装修计划</dd>
        <dd class="ikov"><img src="../../../assets/images/shuaxin.jpg" width="26" height="26"></dd>
        <dd class="u87">
          <router-link tag="input" type="button" :to="{name:'fitTaskInstanceAdd',params:{orderId:orderId}}" value="新增任务"class="dfrk">新增任务</router-link>
        </dd>
        <dd class="u87">
          <router-link tag="input" type="button" :to="{name:'fitOrderSaveAsFitRule',params:{orderId:orderId}}" value="更新发布"class="dfrk">更新发布</router-link>
        </dd>
      </dl>
      <div class="uy7n">
        <div style="padding:5px">
          <div class="tabmenu">
            <dl class="fdr">
              <img src="../../../assets/images/zuo11.jpg" width="24" height="34">
            </dl>
            <ul>
              <li :class="{on:activeNo==-1}" @click="selected1">
                <div class="erdcb">
                  <router-link to="/ownerSiderBar/fitOrderUnPublished/fitPlanRunning/fitPlanTotalView">
                    进度总览
                  </router-link>
                </div>

              </li>
              <li :class="{on:activeNo==phaseNo}" id="PARm1" v-for="phaseNo in phaseCount"
                  @click="selected(phaseNo)">
                <router-link :to="{name:'fitPlanPhase',params:{phaseNumber:phaseNo}}">
                  <dl class="ertfm">
                    <dd class="erdc">第{{phaseNo}}阶段</dd>
                    <dd class="red">地 | 墙</dd>
                  </dl>
                </router-link>
              </li>
            </ul>

            <dl class="ertwc">
              <img src="../../../assets/images/you11.jpg" width="24" height="34">
            </dl>
          </div>
          <!--end tabmenu-->
          <router-view></router-view>
        </div>

      </div>
    </div>

  </div>
</template>
<script>
  import axios from 'axios'
  export default{
    data(){
      return {
        phaseCount: '',
        orderId: 1,
        activeNo:-1
      }
    },
    mounted: function () {
      this.getPhaseCount()
    },
    methods: {
      getPhaseCount(){
        var url = basePath + "/fitOrder/notEditWorkAmount/order/" + this.orderId + "/phaseCount";
        axios.get(url
        ).then(response => (
          this.data = response.data,
            this.phaseCount = this.data
        ))
      },
      selected(phaseNo){
        this.activeNo=phaseNo
      },
      selected1(){
        this.activeNo=-1
      }
    }
  }
</script>



