<template>
  <div class="tabbox" style="clear:both">
    <div id="content0" class="content0" style="display: block;">
      <div class="retw">
        <dl class="iuyt">
          <dd class="ertf">开工</dd>
          <dd class="io9b">2017-05-01</dd>
        </dl>

        <dl class="iuyt11">
          <dd class="ertf">基础装修完工</dd>
          <dd class="io9b">2017-05-01</dd>
        </dl>
      </div>

      <dl class="iuyt88">
        <dd class="ertf">阶段</dd>
        <dd class="io9b">工期（天）</dd>
      </dl>
      <table border="1" class="aa38">
        <tbody style="table-layout:fixed;">
        <tr>
          <td v-for="n in doneDays" width="20" style="background-color:green">&nbsp;</td>
          <td style="width:20px;float:left"><span style="color:green">{{doneDays / workDays * 100 | fix2}}%</span></td>
          <td v-for="n in workDays-doneDays-1" width="20">&nbsp;</td>
        </tr>
        <tr>
          <td :colspan=phase.totalDays v-for="phase in phaseList">
            阶段{{phase.number}}
          </td>
        </tr>
        <tr>
          <td :colspan=phase.totalDays v-for="phase in phaseList">
            {{phase.totalDays}}天
          </td>
        </tr>
        </tbody>
      </table>

      <div class="erfcc">
        <dl class="ehjn">
          <dd class="iu89">产品材料需求清单</dd>
          <dd class="jjgf" @click="showProduct"><a href="javascript:void(0)">共{{totalProductList.length}}种 <img
            src="../../../assets/images/axik.jpg" width="20" height="15"></a></dd>
        </dl>
        <template v-if="productShowList=='on'">
        <table width="649" height="125" border="1" class="aa7">
          <tbody>
          <tr style="background:#f5f5f5;">
            <td width="76">种类</td>
            <td width="96">需求总量<br>
              （单位）
            </td>
            <td width="96">选中材料<br>
              （名称）
            </td>
            <td width="80">选中材料<br>
              （品牌）
            </td>
            <td width="106">选中材料<br>
              （规格）
            </td>
            <td width="110"><p>数量
            </p>
              <p>（单位）</p></td>

          </tr>
          <tr v-for="totalProduct in totalProductList">
            <td height="39"><a href="#">{{totalProduct.categoryName}} </a></td>
            <td>{{totalProduct.amount}}平米 </td>
            <td><a href="#">{{totalProduct.title}} </a></td>
            <td> {{totalProduct.brand}} </td>
            <td>规格1 </td>
            <td>数量1</td>
          </tr>
          </tbody>
        </table>

        </template>
      </div>

      <div class="erfcc">
        <dl class="ehjn">
          <dd class="iu89">工人需求清单</dd>
          <dd class="jjgf" @click="showWorker()"><a href="javascript:void(0)">共{{totalWorkerList.length}}种 <img
            src="../../../assets/images/axik.jpg" width="20" height="15"></a></dd>
        </dl>
        <template v-if="workerShowList=='on'">
        <table  width="290" border="1" class="aa7">
          <tbody>
          <tr style="background:#f5f5f5;">
            <td width="76">工种</td>
            <td width="96">人数</td>
            <td width="96">总工时<br>
              （h/人）
            </td>
          </tr>
          <tr v-for="totalWorker in totalWorkerList">
            <td height="40"><a href="#">{{totalWorker.categoryName}} </a></td>
            <td height="40"> {{totalWorker.amount}} </td>
            <td> {{totalWorker.taskTime}}</td>
          </tr>
          </tbody>
        </table>
        </template>
      </div>

      <div class="erfcc">
        <dl class="ehjn">
          <dd class="iu89">机械设备需求清单</dd>
          <dd class="jjgf" @click="showMachine"><a href="javascript:void(0)">共{{totalMachineList.length}}种 <img
            src="../../../assets/images/axik.jpg" width="20" height="15"></a></dd>
        </dl>
        <template v-if="machineShowList=='on'">
        <table width="488" height="125" border="1" class="aa7">
          <tbody>
          <tr style="background:#f5f5f5;">
            <td width="76">机械种类</td>
            <td width="96">选中机械<br>
              （型号）
            </td>
            <td width="96">选中机械<br>
              （品牌）
            </td>
            <td width="80">需求总量<br>
              （台）
            </td>
            <td width="106">作业时产<br>
              （h）
            </td>
          </tr>
          <tr v-for="totalProduct in totalMachineList ">
            <td height="39"><a href="#">{{totalProduct.categoryName}} </a></td>
            <td><a href="#">{{totalProduct.versionName}} </a></td>
            <td>{{totalProduct.brand}}</td>
            <td>品牌1</td>
            <td>{{totalProduct.taskTime}}</td>
          </tr>
          </tbody>
        </table>
        </template>
      </div>


    </div>

  </div><!--end tabbox-->
</template>
<script>
  import axios from 'axios'
  import Vue from 'vue'
  Vue.filter('fix2', function (value) {
    return value.toFixed(0)
  })
  export default{
    data(){
      return {
        orderId: 1,
        data: '',
        tdBackGround: 'white',
        tdTextColor: 'black',
        workDays: 1,
        doneDays: 0,
        phaseList: [],
        totalProductList: [],
        totalMachineList: [],
        totalWorkerList: [],
        workerShowList: '',
        machineShowList: '',
        productShowList: '',
      }
    },
    mounted: function () {
      this.fetchData();
      if (this.workDays == this.doneDays) {
        this.tdBackGround = 'green'
        this.doneDays = this.doneDays - 1
        this.tdTextColor = 'white'
      }
      else {
        this.tdTextColor = 'black'
      }
    },
    methods: {
      fetchData(){
        var url = basePath + "/fitOrder/notEditWorkAmount/order/" + this.orderId;
        axios.get(url
        ).then(response => (
          this.data = response.data,
            this.phaseList = this.data.phaseList,
            this.workDays = this.data.totalDays,
            this.doneDays = this.data.completeDays,
            this.totalProductList = this.data.totalRequirementProductList,
            this.totalMachineList = this.data.totalRequirementMachineList,
            this.totalWorkerList = this.data.totalRequirementWorkerList
        ))
      },
      showWorker(){
        if(this.workerShowList=='on')
          this.workerShowList=''
        else
          this.workerShowList='on'
      },
      showProduct(){
        if(this.productShowList=='on')
          this.productShowList=''
        else
          this.productShowList='on'
      },
      showMachine(){
        if(this.machineShowList=='on')
          this.machineShowList=''
        else
          this.machineShowList='on'
      },
    }


  }
</script>



