import Vue from 'vue'
import Router from 'vue-router'
import Header from '../../common/components/Header.vue'
import OwnerSiderBar from "../components/OwnerSiderBar.vue"
import FitOrderUnPublished from "../components/fitOrder/FitOrderUnPublished.vue"
import SelectFitPlan from "../components/fitOrder/SelectFitPlan.vue"
import FitPlanRunning from "../components/fitOrder/FitPlanRunning.vue"
import FitPlanPhase from "../components/fitOrder/FitPlanPhase.vue"
import FitPlanTotalView from "../components/fitOrder/FitPlanTotalView.vue"
import FitPlanUneditTotalView from "../components/fitOrder/FitPlanUneditTotalView.vue"
import FitPlanUnedit from "../components/fitOrder/FitPlanUnedit.vue"
import FitPlanPhaseUnedit from "../components/fitOrder/FitPlanPhaseUnedit.vue"
import FitTaskInstanceAdd from "../components/fitOrder/FitTaskInstanceAdd.vue"
import FitOrderLog from "../components/fitOrder/FitOrderLog.vue"
import FitOrderCancel from "../components/fitOrder/FitOrderCancel.vue"
import FitOrderSaveAsFitRule from "../components/fitOrder/FitOrderSaveAsFitRule.vue"
import BaseFitStandard from "../../decorate_template/components/fitTask/BaseFitStandard.vue"
import FitStandardInfo from "../../decorate_template/components/fitTask/FitStandardInfo.vue"
Vue.use(Router);
export default new Router({
  routes: [
    {
      path: '/',
      name: 'Header',
      component: Header,
      children: [
        {
          path: 'ownerSiderBar',
          name: 'ownerSiderBar',
          component: OwnerSiderBar,
          children: [
            {
              path: 'fitOrderUnPublished',
              name: 'fitOrderUnPublished',
              component: FitOrderUnPublished,
              children: [
                {
                  path: 'selectFitPlan',
                  name: 'selectFitPlan',
                  component: SelectFitPlan
                },
                {
                  path: 'fitPlanUnedit',
                  name: 'fitPlanUnedit',
                  component: FitPlanUnedit,
                  redirect: 'fitPlanUnedit/fitPlanUneditTotalView',
                  children: [
                    {
                      path: 'fitPlanUneditTotalView',
                      name: 'fitPlanUneditTotalView',
                      component: FitPlanUneditTotalView,
                    },
                    {
                      path: '/fitPlanPhaseUnedit/:phaseNumber',
                      name: 'fitPlanPhaseUnedit',
                      component: FitPlanPhaseUnedit,
                    },
                  ]
                },
                {
                  path: 'fitPlanRunning',
                  name: 'fitPlanRunning',
                  component: FitPlanRunning,
                  redirect: 'fitPlanRunning/fitPlanTotalView',
                  children: [
                    {
                      path: '/fitPlanPhase/:phaseNumber',
                      name: 'fitPlanPhase',
                      component: FitPlanPhase,
                    },
                    {
                      path: 'fitPlanTotalView',
                      name: 'fitPlanTotalView',
                      component: FitPlanTotalView,

                    }
                  ]
                },
                {
                  path: 'fitOrderCancel',
                  name: 'fitOrderCancel',
                  component: FitOrderCancel
                }
              ]
            }
          ]

        },
        {
          path: '/fitOrderSaveAsFitRule/:orderId',
          name: 'fitOrderSaveAsFitRule',
          component: FitOrderSaveAsFitRule
        },
        {
          path: "/fitTaskInstanceAdd/:orderId",
          name: "fitTaskInstanceAdd",
          component: FitTaskInstanceAdd
        },
        {
          path: "/fitStandardInfo/:fitStandardId",
          name: "fitStandardInfo",
          component: FitStandardInfo
        },
        {
          path: "/add_fit_standard",
          name: "BaseFitStandard",
          component: BaseFitStandard
        },
        {
          path: "/fitOrderLog/:orderId",
          name: "fitOrderLog",
          component: FitOrderLog
        }
      ]
    }
  ]
})



