<script>
  import axios from 'axios'
  export default{
    data(){
      return {
        ifFitPlanSelectBlock: 'block',
        data: '',
        orderId: 1,
        fitManager: '',
        fitOrderLog: '',
        fitOrder: '',
        ownerInfo:''
      }
    },
    mounted: function () {
      this.getData()
    },
    methods: {
      getData(){
        var url = basePath + "/fitOrder/notIssuedFitPlan/" + this.orderId;
        axios.get(url
        ).then(response => (
          this.data = response.data,
            this.fitManager = this.data.fitManager,
            this.fitOrderLog = this.data.fitOrderLog,
            this.fitOrder = this.data.fitOrder,
            this.ownerInfo=this.data.ownerInfo
        ))
      }
    }
  }
</script>



