<template>
  <div class="center">
  <div class="main1">
    <div class="zong">
      <dl class="tgvn"><dd class="ery">查看执行信息</dd>
      </dl>

      <div class="erfcvb">
        <div class="wrtef">

          <dl :class="{iutre:index!=0,iutre11:index==0}" v-for="(log,index) in logList">
            <dd class="erty">{{log.operationDetail}}：{{log.operatorRole}}  <a href="javascript:void(0)">{{log.operatorName}}</a> 手机号：{{log.operatorPhone}}</dd>
            <dd class="iu8oo">{{log.operationTime}}</dd>
          </dl>
        </div>


      </div>
    </div>
  </div>
  </div>
</template>
<script>
  import axios from 'axios'
 export default{
  data(){
      return {
        orderId:'',
        logList:[]
      }
  },
   mounted(){

     this.orderId=this.$route.params.orderId,
     this.getLogList()
   },
   methods:{
       getLogList(){

          var url = basePath + "/fitOrder/log/orderId/"+this.orderId;
               axios.get(url).then(
               response=>(
                 this.logList=response.data
               )
           )
       }
   }
 }
</script>



